body {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

ul{
    list-style-type: none;
    line-height: 2rem;
}

ul li{
    font-size: 1.125rem;
}

.fa-solid {
    cursor: pointer;
}

select {
    border: none;
    padding: 0.3rem;
    cursor: pointer;
}


/* HEADER DESIGN */

.nav-link {
    color: black;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.offer-contain h1 {
    text-align: center;
}


.fa-circle-check {
    margin-right: 10px;
    color: green;
}

.fa-cart-plus {
    margin-right: 10px;
}

/* HEADER DESIGN */

/* HOME DESIGN */

.home-main {
    background-color: #f8f9fa;
}

.discover-row h2 {
    text-align: center;
    font-size: 3.5rem;
}

.discover-link {
    font-size: 1.12rem;
}


.row-banner {
   display: flex;
   align-items: center;
   justify-content: center;
   

   
}

.row-banner > p {
    position: absolute;
    font-size: 5rem;
    color: #fff; 
    z-index: 1;
    
} 

.life-style {
    position: relative;
    height: 45rem;
    width: 100%;
    object-fit: cover;
    filter: brightness(70%);
    
}

.evaluare-img {
    object-fit: cover;
    width: 95%;
    
}


.client-area {
    text-align: center;
}

.client-area > h1  {
    color: #5FBC44;
    font-weight: 400;
}

.client-area > p {
    font-size: 1.5rem;
}
   
.client-img {
    width: 35rem;
    margin: 0 auto;
}

.linkto {
    text-decoration: none;
    color: black;
}




/* HOME DESIGN */

/* PRODUCTS LIST DESIGN */

.colloum-one {
    border: 1px solid transparent;
    background-color: #f8f9fa;
    
   
    
}

.colloum-two {
    border: 1px solid transparent;

   
    
}

.main-card-title  {
    font-size: 1rem;
}

.main-card-desc  {
    font-size: 0.90rem;
}


.header-clisting {
    display: flex;
    justify-content: space-between;
    border: 1px solid transparent;
    margin-bottom: 1rem;
    padding: 0.6rem;
    background-color: #f8f9fa;

}

.header-clisting span{
    font-size: 1rem;
}

.filtru-categorii {
    
    width: 99%;
    padding: .5rem;
    
}

.btn {
    width: 80%;
}

.side-col-title {
    font-size: .8rem;

}

.side-col-desc {
    font-size: .9rem;
}

/* PRODUCTS LIST DESIGN */

.contact-us {
    border: 1px solid transparent;
    box-shadow: 0 0 30px -5px rgba(0, 0, 0, 0.2);
}
/* FOOTER DESIGN */

footer {

    background-color: #f8f9fa;
    padding: 20px 0;
    color: black;
}

.created {
    text-align: center;
    font-size: 1.2rem;
    border-top: 1px solid;
    padding-top: .8rem;
}

.footer-information {
    justify-content: space-between;
}

